/*====================================================*/
body {
  font-family: "PT Sans", sans-serif;
  font-size: 2rem; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

.sinScroll {
  overflow: hidden; }

a {
  text-decoration: none; }

img {
  width: 100%; }

h1, h2, h3, h4 {
  margin: 0; }

h1 {
  font-size: 4.5rem; }

h2 {
  font-size: 4rem; }

h3 {
  font-size: 3.8rem; }

h4 {
  font-size: 3rem; }

.centrar-texto {
  text-align: center; }

.font-AzulOscuro {
  color: #293b5f; }

.font-Verde {
  color: #939b62; }

.font-Naranja {
  color: #fc8621; }

.legend {
  font-weight: 700;
  text-transform: uppercase; }

.campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-top: 1rem; }
  .campo input, .campo textarea, .campo select {
    background-color: #c8c2bc;
    width: 30rem;
    border: none;
    border-radius: .5rem; }

.btn-principal {
  background-color: #293b5f;
  color: #fff;
  font-weight: 700;
  border: none;
  margin-top: 2rem;
  padding: 1rem 2rem;
  border-radius: 1rem;
  width: 20rem;
  cursor: pointer; }
  .btn-principal:hover {
    background-color: #939b62;
    color: #1a1c20; }

/*============= TABLA VISUALIZAR ELEMENTOS =============*/
.tabla-MostrarDatos {
  width: 100%;
  background-color: #c8c2bc;
  border-radius: 1rem;
  -webkit-box-shadow: 1px 1px 12px 0px #323232;
  -moz-box-shadow: 1px 1px 12px 0px #323232;
  box-shadow: 1px 1px 12px 0px #323232; }
  .tabla-MostrarDatos thead {
    width: 100%;
    color: #fc8621; }
  .tabla-MostrarDatos tbody td {
    padding: 1.25rem;
    border-bottom: solid 3px black; }
  .tabla-MostrarDatos tbody tr:last-of-type td {
    border-bottom: none; }
  .tabla-MostrarDatos .titulos-column {
    font-weight: 700; }

/*============= ELIMINAR ELEMENTOS =============*/
.backgroundIndex-img {
  background-image: url(../img/indexBackground-img.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  background-position: center center; }

.loginIndex {
  background-color: rgba(255, 255, 255, 0.9);
  margin: 14rem auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 1rem;
  width: 30rem;
  -webkit-box-shadow: 7px 7px 8px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 7px 7px 8px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 7px 7px 8px 0px rgba(50, 50, 50, 0.75); }
  .loginIndex img {
    width: 20rem; }
  @media (min-width: 768px) {
    .loginIndex {
      width: 35rem; } }

.loginIdex-formulario {
  padding: 1.66667rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.campo-login {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .campo-login input {
    border: none;
    padding: .5rem;
    border-radius: .5rem;
    background-color: #c8c2bc;
    margin-bottom: 2.5rem; }

.btn-ingresar {
  border: none;
  border-radius: 1rem;
  background-color: #293b5f;
  color: #fff;
  font-weight: 700;
  padding: 1rem 3rem;
  cursor: pointer;
  margin-top: 1.5rem; }
  .btn-ingresar:hover {
    background-color: #fc8621;
    color: #1a1c20; }

.error {
  color: red; }

.crearUsuario-enlace {
  color: #939b62;
  font-weight: 700;
  margin-top: 1rem; }
  .crearUsuario-enlace:hover {
    color: #fc8621; }

.header-envatecHome {
  background-color: #c8c2bc;
  -webkit-box-shadow: 0px 5px 7px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 0px 5px 7px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 0px 5px 7px 0px rgba(50, 50, 50, 0.75); }

.header-envatecHome-contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 5rem; }
  @media (min-width: 1200px) {
    .header-envatecHome-contenido {
      flex-direction: row;
      justify-content: space-between; } }

.navegacionPrincipal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0 1rem 0;
  font-weight: 700; }
  .navegacionPrincipal a {
    padding-top: 1rem;
    color: #293b5f; }
  @media (min-width: 1200px) {
    .navegacionPrincipal {
      flex-direction: row;
      padding-top: 0; }
      .navegacionPrincipal a {
        margin-left: 2rem; } }
  .navegacionPrincipal a:hover {
    color: #939b62;
    border-bottom: solid 0.3rem #fc8621; }
  .navegacionPrincipal .here {
    border-bottom: solid 0.3rem #fc8621;
    color: #939b62; }

.header-envatecHome-logo img {
  width: 10rem; }

/*======= MAIN EQUIPOS DE COMPUTO =========*/
.equiposComputo {
  padding: 2.5rem; }

.agregar-equipoComputo {
  padding-top: 2rem; }

@media (min-width: 1200px) {
  .formulario-agregar-equipoComputo {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.66667rem; }
    .formulario-agregar-equipoComputo .btn-principal {
      grid-column: 2 / 3;
      margin: 0 auto; } }

.perifericos {
  padding-top: 5rem; }

.formulario-agregar-periferico {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 1200px) {
    .formulario-agregar-periferico {
      align-items: initial;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 1.66667rem; }
      .formulario-agregar-periferico .btn-principal {
        grid-column: 2 / 3;
        margin: 0 auto; } }

.perifericos {
  padding: 5rem 0 5rem 0; }

.eliminar-actualizar-periferico {
  padding-bottom: 5rem; }
  @media (min-width: 1200px) {
    .eliminar-actualizar-periferico {
      display: grid;
      grid-template-columns: 30% 70%;
      padding-left: 1.66667rem; } }

.eliminar-periferico-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.actualizar-periferico-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }
  @media (min-width: 1200px) {
    .actualizar-periferico-form {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 1.66667rem;
      align-items: center;
      justify-content: center; }
      .actualizar-periferico-form .btn-principal {
        width: 25rem;
        margin: 0 auto; } }

.actualizar-periferico {
  margin-top: 2rem; }
  @media (min-width: 1200px) {
    .actualizar-periferico {
      margin-top: 0; } }
